.div-nav {
    background-color: var(--color-nav-bg);
    width: 13%;
    height: 100vh;
    line-height: 1.5em;
	
}


button{
    background-color: var(--color-nav-bg);
    outline: none;
    text-decoration: none;
    border: none;
}

.nav-icon {
    color: var(--color-white-smoke);
    font-size: 18px;
    font-weight: 500;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    border-top: var(--color-border) solid 1px;
}

.activeLink {
    color: var(--color-white-grey);
    background-color: var(--color-gren-white);
    text-decoration: none;
    border-right: 5px solid var(--color-border);
}

.nav-icon:hover{
    border-right: 5px solid var(--color-border);
    color: var(--color-white-smoke) ;
}

.image {
    border: 2px solid var(--color-border);
    max-width: 80px;
    max-height: 77px;
    text-align: center;
    margin: 25px auto 10px;
}
.imgs {
    height: 77px;
    width: 80px;
    background-size: 100%;
    overflow: hidden;
}

h1{
    font-size: 20px;
    font-weight: 400;
    line-height: 1.2em;
    margin: 0;
}
.name {
    color: var(--color-white);
    font-weight: 500;
    text-align: center;
    margin: 0 0 20px 0;
    margin: 0;
    height: 100%;
    line-height: 1.2em;
}

.menu {
    display: none;
}
.dropdown {
    color: var(--color-border);
}


@media only screen and (max-width: 1045px) {
    
    .navItems--hidden {
        display: none;   
    }
    .menu {
        display: block;
    }

    .photo-name {
        display: flex;
        justify-content: space-between;
        margin-right: 10px;
    }

    .div-nav {
        overflow: visible;
        position: fixed;
        width: 100%;
        height: 50px;
        border-bottom: 1px solid #eee;
        background-color: var(--color-nav-bg);
        z-index: 1000;
    }
    .name {
        display: inline-block;
        font-size: 18px;
        font-weight: 400;
        margin: 0;
        
        line-height: 50px;
    }
    .image {
        display: none;
    }

    .nav-icon {
        background-color: var(--color-nav-bg);
    }
    .nav-icon:hover{
        border-bottom: 5px solid var(--color-border);
        border-right: none;
        color: var(--color-white-grey);
    }
    .activeLink {
        border-bottom: 5px solid var(--color-border);
        border-right: none;
        color: var(--color-white-grey);
    }
}